<template>
  <div id="app">


<!-- ############ PAGE START-->
<div class="app-body-inner">
  <div class="row-col row-col-xs b-b">
    <!-- column -->
    <div class="col-sm-1 col-md-1 w  b-r">
      <div class="row-col">
        <div class="row-row">
          <div class="row-body scrollable hover">
            <div class="row-inner">
              <div class="nav nav-pills nav-stacked m-t-sm flex-column">
                <a class="nav-link _500">Presentation 1</a>
              </div>
            </div>
          </div>
        </div>
<!--        <div class="p-a">
          <span bs-tooltip title="Double click to Edit" class="pull-right text-muted inline p-a-xs m-r-sm"><i class="fa fa-question"></i></span>
          <a href class="btn btn-sm white"><i class="fa fa-plus fa-fw m-r-xs"></i> <span class="hidden-sm-down">New Group</span></a>
        </div> -->
      </div>
    </div>
    <!-- /column -->
    <!-- column -->
    <div class="col-sm-1 col-md-1 light bg b-r">
      <div class="row-col">
        <div class="row-row">
          <div class="row-body scrollable hover">
            <div class="row-inner">
              <div class="list inset">
                <div class="list-item pointer" v-on:click.prevent="onEdit(s)" v-for="(s, index) in slides">
<!--                  <div class="list-left">
                    <span class="w-40 avatar">
                      <img src="/static/assets/images/a0.jpg" class="img-circle">
                    </span>
                  </div> -->
                  <div class="list-body">
                    {{ s.header }}
                    <small class="block">{{ s.type }} </small>
                    <a href v-on:click.prevent="onDelete(s)" class="btn btn-sm white btn-addon"><i class="fa fa-times fa-fw m-r-xs"></i> Delete</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="p-a b-t text-center">
          <a href v-on:click.prevent="onAddNew()" class="btn btn-sm white btn-addon"><i class="fa fa-plus fa-fw m-r-xs"></i> New Song</a>
        </div>
      </div>
    </div>
    <!-- /column -->

    <!-- column -->
    <div class="col-sm-10 col-md-10">
      <div class="row-col">
        <div class="p-a-sm">
          <div>
            <a class="btn btn-sm white pull-right"><i class="fa fa-times"></i></a>
            <a class="btn btn-sm white">Edit</a>
          </div>
        </div>
        <div class="row-row">
          <div class="row-body">
            <div class="row-inner">
              <div class="padding">
                <!--
                <div class="row h-auto m-b-lg">
                  <div class="col-sm-3">
                    <div class="avatar w-64 inline">
                      <img src="/static/assets/images/a0.jpg">
                    </div>
                  </div>
                  <div class="col-sm-9 v-m h2 _300">
                    <div class="p-l-xs">
                      <input type="text" placeholder="First" class="form-control w-sm inline" >
                      <input type="text" placeholder="Last" class="form-control w-sm inline">
                    </div>
                  </div>
                </div>
                -->
                    
                <form v-on:submit.prevent="onSubmit">
                <!-- fields -->
                <div class="form-horizontal">
                  <div class="form-group row">
                    <label class="col-sm-3 form-control-label">Header</label>
                    <div class="col-sm-9">
                      <input name="header" type="text" placeholder="header" v-model="slide.header" class="form-control"/>
                    </div>
                  </div>
                  <div class="form-group row">
                    <label class="col-sm-3 form-control-label">Content</label>
                    <div class="col-sm-9">
                      <textarea name="content" type="text" placeholder="content" v-model="slide.content" class="form-control" rows="10"></textarea>
                    </div>
                  </div>
                  <div class="form-group row">
                    <label class="col-sm-3 form-control-label">Type</label>
                    <div class="col-sm-9">
                      <select name="type" placeholder="type" v-model="slide.type" class="form-control">
                        <option value="song">Song</option>
                        <option value="bible">Bible</option>
                      </select>
                    </div>
                  </div>
                  <div class="form-group row">
                    <label class="col-sm-3 form-control-label">Seq</label>
                    <div class="col-sm-9">
                      <input name="seq" type="text" placeholder="sequence" v-model="slide.seq" class="form-control"/>
                    </div>
                  </div>
                  <hr>
                  <div class="form-group row">
                    <label class="col-sm-3 form-control-label">Align</label>
                    <div class="col-sm-3">
                      <select name="align" placeholder="align" v-model="slide.align" class="form-control">
                        <option value="left">Left</option>
                        <option value="center">Center</option>
                        <option value="right">Right</option>
                      </select>
                    </div>
                    <label class="col-sm-3 form-control-label">Font Size</label>
                    <div class="col-sm-3">
                      <input name="fontsize" type="text" placeholder="1em" v-model="slide.fontsize" class="form-control"/>
                    </div>
                  </div>
                  <div class="form-group row">
                    <label class="col-sm-3 form-control-label">Top offset</label>
                    <div class="col-sm-3">
                      <input name="toffset" type="text" placeholder="offset" v-model="slide.toffset" class="form-control"/>
                    </div>
                    <label class="col-sm-3 form-control-label">Left offset</label>
                    <div class="col-sm-3">
                      <input name="loffset" type="text" placeholder="offset" v-model="slide.loffset" class="form-control"/>
                    </div>
                  </div>
                  <div class="form-group row">
                    <label class="col-sm-3 form-control-label">Color</label>
                    <div class="col-sm-3">
                      <input name="color" type="color" placeholder="color" v-model="slide.color"/>
                    </div>
                    <label class="col-sm-3 form-control-label">Shadow</label>
                    <div class="col-sm-3">
                      <input name="shadow" type="color" placeholder="shadow" v-model="slide.shadow"/>
                    </div>
                  </div>
                  <div class="form-group row">
                    <label class="col-sm-3 form-control-label">Background</label>
                    <div class="col-sm-9">
                      <input name="background" type="text" placeholder="background" v-model="slide.background" class="form-control" >
                    </div>
                  </div>
                  
                    <button v-if="updating">Update</button>
                    <button v-else>Add</button>

                </div>
                <!-- / fields -->
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- /column -->
  </div>
</div>

<!-- ############ PAGE END-->



  </div>
</template>


<script>
import toastr from 'toastr'
import Firebase from 'firebase'

var config = {
  apiKey: 'AIzaSyDmTyMhZx-xH3R5UmzpTUiDXD27hYk9P1w',
  authDomain: 'ccstation-api.firebaseapp.com',
  databaseURL: 'https://ccstation-api.firebaseio.com',
  projectId: 'ccstation-api',
  storageBucket: 'ccstation-api.appspot.com',
  messagingSenderId: '806524872403'
}

var app = Firebase.initializeApp(config, 'app1')
let db = app.database()
let slidesRef = db.ref('slides')

export default {
  name: 'app',
  firebase: {
    slides: slidesRef.orderByChild('seq')
  },

  data () {
    return {
      title: 'Presentations',
      updating: false,
      updateIndex: 0,
      slide: {
        header: '',
        content: '',
        color: '#ffffff',
        shadow: '#ffffff',
        fontsize: '',
        align: '',
        toffset: 0,
        loffset: 0,
        background: '',
        type: '',
        seq: 0
      }
    }
  },

  methods: {
    onSubmit: function () {
      if (this.updating) {
        this.onUpdate()
        return
      }
      slidesRef.push(this.slide)
      this.slide.header = ''
      this.slide.content = ''
      this.slide.color = '#ffffff'
      this.slide.shadow = '#ffffff'
      this.slide.fontsize = ''
      this.slide.align = ''
      this.slide.toffset = 0
      this.slide.loffset = 0
      this.slide.background = ''
      this.slide.type = ''
      this.slide.seq = 0
    },
    onEdit: function (slide) {
      this.updating = true
      this.updateIndex = slide['.key']
      this.slide.header = (slide.header === undefined) ? '' : slide.header
      this.slide.content = (slide.content === undefined) ? '' : slide.content
      this.slide.color = (slide.color === undefined) ? '#ffffff' : slide.color
      this.slide.shadow = (slide.shadow === undefined) ? '#ffffff' : slide.shadow
      this.slide.fontsize = (slide.fontsize === undefined) ? '' : slide.fontsize
      this.slide.align = (slide.align === undefined) ? '' : slide.align
      this.slide.toffset = (slide.toffset === undefined) ? 0 : slide.toffset
      this.slide.loffset = (slide.loffset === undefined) ? 0 : slide.loffset
      this.slide.background = (slide.background === undefined) ? '' : slide.background
      this.slide.type = (slide.type === undefined) ? '' : slide.type
      this.slide.seq = (slide.seq === undefined) ? 0 : slide.seq
    },
    onUpdate: function () {
      this.updating = false
      console.log(this.slide)
      delete this.slide['.key']
      slidesRef.child(this.updateIndex).update(this.slide)
      this.slide.header = ''
      this.slide.content = ''
      this.slide.color = '#ffffff'
      this.slide.shadow = '#ffffff'
      this.slide.fontsize = ''
      this.slide.align = ''
      this.slide.toffset = 0
      this.slide.loffset = 0
      this.slide.background = ''
      this.slide.type = ''
      this.slide.seq = 0
    },
    onDelete: function (slide) {
      // Remove one item starting at
      // the specified index
      slidesRef.child(slide['.key']).remove()
      toastr.success('Slide removed successfully')
    },
    onAddNew: function () {
      this.updating = false
      this.slide.header = ''
      this.slide.content = ''
      this.slide.color = '#ffffff'
      this.slide.shadow = '#ffffff'
      this.slide.fontsize = ''
      this.slide.align = ''
      this.slide.toffset = 0
      this.slide.loffset = 0
      this.slide.background = ''
      this.slide.type = ''
      this.slide.seq = 0
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



